<template>
  <div class="course-random">
    <head-top :title="'第' + realPickIndex + '次随机挑人'">
    </head-top>

    <div class="sub_nav">
      <span>选中成员</span>
      <van-button v-show="randomStudents.length>0 && randomPickState === 1" type="danger" size="small"
                  :disabled="isLoading" @click="stop">
        结束挑人
      </van-button>
    </div>
    <div class="sub_module">
      <span v-if="randomStudents.length===0">暂无数据</span>
      <div class="flex-wrap-between" v-else>
        <div v-for="(sub, k) in randomStudents" :key="k" class="student_card van-hairline--surround">
          <img :src="sub.imgSrc" class="col_4" alt=""/>
          <div>{{sub.studentName}}</div>
        </div>
        <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - randomStudents.length % 4) % 4"
             :key="-d">
        </div>
      </div>
    </div>

    <div class="sub_nav">
      <span>课堂成员</span>
      <van-radio-group v-model="studentType" direction="horizontal">
        <van-radio :name="1">全部</van-radio>
        <van-radio :name="2">签到</van-radio>
      </van-radio-group>
      <van-button type="primary" size="small" :disabled="isLoading || randomStudents.length>= studentSelectedList.length"
                  @click="toRandom">
        {{randomStudents.length>0?'重新挑人':'开始挑人'}}
      </van-button>
    </div>
    <div class="sub_module">
      <span v-if="studentSelectedList.length===0">暂无数据</span>
      <div class="flex-wrap-between" v-else>
        <div v-for="(sub, k) in studentSelectedList" :key="k" class="student_card van-hairline--surround"
             :class="{'active':activeIndex===k }">
          <img :src="sub.imgSrc" class="col_4" alt=""/>
          <div>{{sub.studentName}}</div>
        </div>
        <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - studentSelectedList.length % 4) % 4" :key="-d">
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import defaultImg from "../../../../assets/boy_img.png";

export default {
  name: "randomPerson",
  data () {
    return {
      studentList: [],
      randomStudents: [],
      activeIndex: -1,
      isLoading: false,

      studentType: 1,
      randomPickIndex: 1,
      randomPickID: "",
      randomPickState: 1, // 1进行中 ,2结束状态
      pickStudentID: ""
    };
  },
  computed: {
    realPickIndex () {
      return this.randomPickIndex + (this.randomPickState === 2 ? 1 : 0);
    },
    studentSelectedList () {
      if (this.studentType === 1) {
        return this.studentList;
      } else {
        return this.studentList.filter(r => r.signState === 1)
      }
    }
  },
  mounted () {
    this.getStudent();
    this.getRandomInfo();
  },
  methods: {
    getStudent () {
      this.studentList = [];
      this.$gatewayHttp.student.getDataList().then(res => {
        if (res.Result && res.Datas) {
          res.Datas.forEach(item => {
            this.studentList.push({
              imgSrc: defaultImg,
              studentID: item["StudentID"],
              studentName: item["StudentName"],
              studentSno: item["StudentXH"],
              signState: item["SignState"]
              // studentSex: item["StudentXH"]
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getRandomInfo () {
      this.$gatewayHttp.randomPick.getDataInfo({
        ciID: this.$store.getters.courseID
      }).then(res => {
        if (res.Result && res.Data) {
          this.randomPickIndex = res.Data["RPSerial"];
          this.randomPickID = res.Data["RPID"];
          this.randomPickState = res.Data["State"];
          this.getRandomList();
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getRandomList () {
      this.randomStudents = [];
      this.$gatewayHttp.randomPick.getSelectResult({
        rpID: this.randomPickID,
        rpState: this.randomPickState
      }).then(res => {
        if (res.Result && res.Datas) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });
          res.Datas.forEach(item => {
            this.randomStudents.push({
              imgSrc: defaultImg,
              studentID: item["StudentID"],
              studentName: item["StudentName"],
              studentSno: item["StudentXH"]
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    toRandom () {
      this.pickStudentID = "";
      const interval = setInterval(() => {
        this.activeIndex = this.randomIndex(this.studentList.length - 1);
      }, 90);
      this.$gatewayHttp.randomPick.startPick({
        studentType: this.studentType
      }).then(res => {
        if (res.Result) {
          this.randomPickState = 1;
          this.pickStudentID = res["StudentID"];
          let index = this.studentList.findIndex(r => r.studentID === this.pickStudentID);
          this.activeIndex = index;
          if (index > -1) this.randomStudents.push(this.studentList[index]);
          // console.log(this.randomStudents);
          setTimeout(() => {
            this.activeIndex = -1;
          }, 90);
          this.$toast("挑选成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("开始挑人失败");
        }
        clearInterval(interval);
      }).catch(() => {
        this.$toast("操作失败");
        clearInterval(interval);
      });
    },
    randomIndex (max) {
      return Math.round(max * Math.random());
    },
    stop () {
      this.$gatewayHttp.randomPick.stopPick().then(res => {
        if (res.Result) {
          this.$toast("结束挑人成功");
          this.$router.replace("/course/power");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("结束挑人失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  },
  beforeDestroy () {
    clearTimeout();
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-random {
  min-height: 100%;
  background-color: #fff;

  .active {
    border: 1px solid #ee0a24;
  }
}
</style>
